<template>
    <div>
        <van-nav-bar
                title=""
                @click-left="$router.go(-1)"
                left-arrow style="overflow: auto;background: #fff6ef"
        />
        <van-form @submit="registerUser">
            <van-field
                    v-model="mobile"
                    name=""
                    label=""
                    placeholder=""
                    :rules="[{ required: true, message: '' }]"
            />
            <van-field
                    v-model="password"
                    type="password"
                    name=""
                    label=""
                    placeholder=""
                    :rules="[{ required: true, message: '' }]"
            />
            <van-field
                    v-model="rePassword"
                    type="password"
                    name=""
                    label=""
                    placeholder=""
                    :rules="[{ required: true, message: '' },
                        { validator:rePasswordValidate, message: '' }
                    ]"
            />
            <van-collapse v-model="enhance">
                <van-collapse-item title="//" name="enhanceActive">
                    <van-tabs v-model="identity" color="#ff8000">
                        <van-tab :name="0" title="">
                            <van-field
                                    v-model="username"
                                    name=""
                                    label=""
                                    placeholder=""
                            />
                            <van-field
                                    name=""
                                    label=""
                            >
                                <template #input>
                                    <van-radio-group v-model="gender" direction="horizontal">
                                        <van-radio checked-color="#ff9933" name=""></van-radio>
                                        <van-radio checked-color="#ff9933" name=""></van-radio>
                                    </van-radio-group>
                                </template>
                            </van-field>
                            <van-field
                                    v-model="truename"
                                    name=""
                                    label=""
                                    placeholder=""
                            />
                        </van-tab>
                        <van-tab :name="1" title="">
                            <van-field
                                    v-model="username"
                                    name=""
                                    label=""
                                    placeholder=""
                                    :rules="[{ validator:userNameValidate, message: '' }]"
                            />
                            <van-field
                                    name=""
                                    label=""
                                    :rules="[{ validator:genderValidate, message: '' }]"

                            >
                                <template #input>
                                    <van-radio-group v-model="gender" direction="horizontal">
                                        <van-radio checked-color="#ff9933" name=""></van-radio>
                                        <van-radio checked-color="#ff9933" name=""></van-radio>
                                    </van-radio-group>
                                </template>
                            </van-field>
                            <van-field
                                    v-model="truename"
                                    name=""
                                    label=""
                                    placeholder=""
                                    :rules="[{ validator:trueNameValidate, message: '' }]"

                            />
                        </van-tab>
                        <van-tab :name="2" title="">
                            <van-field
                                    v-model="username"
                                    name=""
                                    label=""
                                    placeholder=""
                                    :rules="[{ validator:userNameValidate, message: '' }]"
                            />
                            <van-field
                                    name=""
                                    label=""
                                    :rules="[{ validator:genderValidate, message: '' }]"

                            >
                                <template #input>
                                    <van-radio-group v-model="gender" direction="horizontal">
                                        <van-radio checked-color="#ff9933" name=""></van-radio>
                                        <van-radio checked-color="#ff9933" name=""></van-radio>
                                    </van-radio-group>
                                </template>
                            </van-field>
                            <van-field
                                    v-model="truename"
                                    name=""
                                    label=""
                                    placeholder=""
                                    :rules="[{ validator:trueNameValidate, message: '' }]"

                            />
                        </van-tab>
                    </van-tabs>
                </van-collapse-item>
            </van-collapse>
            <div style="margin: 16px;">
                <van-button
                        round
                        block
                        native-type="submit"
                        color="#ff9933"
                >
                    
                </van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
    import {Toast} from "vant";

    export default {
        name: "Register",
        data: () => ({
            mobile: '',
            password: '',
            rePassword: '',
            rememberMe: true,
            identity: 0,
            enhance: [],
            username: '',
            truename: '',
            gender: null
        }),
        methods: {
            rePasswordValidate() {
                return this.rePassword === this.password
            },
            userNameValidate() {
                if (this.identity === 0) {
                    return true
                }
                return this.username !== ''
            },
            genderValidate() {
                if (this.identity === 0) {
                    return true
                }
                return this.gender !== null
            },
            trueNameValidate() {
                if (this.identity === 0) {
                    return true
                }
                return this.truename !== ''
            },
            registerUser() {
                let upload = {
                    username: this.username,
                    mobile: this.mobile,
                    password: this.password,
                    truename: this.truename,
                    gender: this.gender,
                    identity: this.identity
                }
                this.axios({
                    method: 'post',
                    url: '/user/register',
                    data: upload,
                    headers: {'content-type': 'application/json'}
                })
                    .then(
                        res => {
                            if (res.data.code === 0) {
                                Toast({message: '', type: 'success'})
                                if(upload.identity===1||upload.identity===2){
                                    setTimeout(
                                        ()=>this.toastPreRegister(),
                                        1000
                                    )
                                }
                            } else {
                                Toast({message: '', type: 'fail'})
                            }
                        }
                    ).catch(
                    () => Toast({message: '', type: 'fail'})
                )
            },
            toastPreRegister(){
                Toast({message: '', type: 'success'})
            }

        }
    }
</script>

