<template>
    <van-cell-group title=" tips:<----">
        <van-cell is-link @click="openAddDialog">
            <template #title>
                <van-row :gutter="20">
                    <van-col>
                        <span style="font-size: 36px;color: #c2a078">+</span>
                    </van-col>
                    <van-col>
                        <span style="font-size: 18px;color: #cac4c4"></span>
                    </van-col>
                </van-row>
            </template>
        </van-cell>
        <van-dialog
                @confirm="handleCommit"
                v-model="addAddressDialog" title="" show-cancel-button>
            <van-form>
                <van-field
                        placeholder=""
                        autofocus label=""
                        :rules="[{ required: true, message: '' }]"
                        v-model="newAddress.username"
                        clearable
                ></van-field>
                <van-field
                        name=""
                        label=""
                >
                    <template #input>
                        <van-radio-group v-model="newAddress.gender" direction="horizontal">
                            <van-radio name=""></van-radio>
                            <van-radio name=""></van-radio>
                        </van-radio-group>
                    </template>
                </van-field>
                <van-field
                        placeholder=""
                        autofocus label=""
                        v-model="newAddress.mobile"
                        :rules="[{ required: true, message: '' }]"
                        clearable
                ></van-field>
                <van-field
                        placeholder=""
                        autofocus label=""
                        :rules="[{ required: true, message: '' }]"
                        v-model="newAddress.adress"
                        maxlength="25"
                        show-word-limit
                        clearable
                ></van-field>
            </van-form>
        </van-dialog>
        <van-swipe-cell v-for="(item,index) in userAddressList" :key="item.id">
            <template #right>
                <van-button @click="removeAddress(item.id)" style="height:100%" square type="danger" text=""/>
                <van-button @click="alterAddress(item)" style="height:100%" square type="warning" text=""/>
            </template>
            <van-cell>
                <template #title>
                    <van-row :gutter="20">
                        <van-col>
                            <van-radio-group v-model="selectAddress">
                                <van-radio :name="index"></van-radio>
                            </van-radio-group>
                        </van-col>
                        <van-col :span="20">
                            <van-row>
                                <span style="font-size: 16px;">{{item.adress}}</span>
                            </van-row>
                            <van-row :gutter="5">
                                <van-col :offset="2">
                                    <span style="font-size: 20px;">{{item.username}}</span>
                                </van-col>
                                <van-col>
                                    <span style="font-size:10px;color:#696969">{{item.gender}}</span>
                                </van-col>
                                <van-col>
                                    <span>{{item.mobile}}</span>
                                </van-col>
                            </van-row>
                        </van-col>
                    </van-row>
                </template>
            </van-cell>
        </van-swipe-cell>
    </van-cell-group>
</template>

<script>
    import {mapActions, mapGetters} from "vuex";
    import {Toast} from "vant";

    export default {
        name: "ChooseAddress",
        computed: {
            ...mapGetters('customer', {
                userAddressList: 'userAddressList'
            }),
            ...mapGetters('validate', {
                userInfo: 'userInfo'
            })
        },
        data: () => ({
            selectAddress: null,
            addAddressDialog: false,
            newAddress: {
                "userId": null,
                "username": "",
                "gender": "",
                "mobile": "",
                "adress": ""
            },
            dialogType: 'add'
        }),
        methods: {
            ...mapActions('customer', {
                requestUserAddressList: 'requestUserAddressList',
            }),
            apiRequestAddAddress() {
                let address = this.newAddress
                address.userId = this.userInfo.id
                console.log(address)
                this.axios({
                    url: '/useraddress/save',
                    method: 'post',
                    data: address,
                    headers: {'content-type': 'application/json'}
                })
                    .then(
                        res => {
                            if (res.data.code === 0) {
                                Toast('')
                                this.requestUserAddressList(this.userInfo.id)
                            } else {
                                Toast({message: '', type: 'fail'})
                            }
                        }
                    ).catch(
                    ()=>{
                        Toast({message: '', type: 'fail'})

                    }
                )
            },
            removeAddress(addressId) {
                this.axios.delete('/useraddress/' + addressId)
                    .then(
                        res => {
                            if (res.data.code === 0) {
                                this.requestUserAddressList(this.userInfo.id)
                            }
                        }
                    )
                    .catch(
                        () => Toast('')
                    )
            },
            alterAddress(address) {
                this.dialogType = 'update'
                this.newAddress = {...address}
                this.addAddressDialog = true
            },
            openAddDialog() {
                this.dialogType = 'add'
                this.addAddressDialog = true
            },
            handleCommit() {
                if (this.dialogType === 'add') {
                    this.apiRequestAddAddress()
                } else if (this.dialogType === 'update') {
                    this.apiRequestUpdateAddress()
                }
            },
            apiRequestUpdateAddress() {
                let address = this.newAddress
                address.userId = this.userInfo.id
                this.axios({
                    method:'put',
                    url:'/useraddress/update',
                    data:address,
                    headers: {'content-type': 'application/json'}
                }).then(
                    res => {
                        if (res.data.code === 0) {
                            Toast('')
                            this.requestUserAddressList(this.userInfo.id)
                        } else {
                            Toast({message: '', type: 'fail'})
                        }
                    }
                ).catch(
                    ()=>{
                        Toast({message: '', type: 'fail'})
                    }
                )
            }
        },
    }
</script>

