<template>
    <div>
        <van-nav-bar
                title=""
                @click-left="$router.push('/')"
                left-arrow style="overflow: auto;background: #fff6ef"
                right-text=""
                @click-right="$router.push('/register')"
        />
        <van-form @submit="loginAndBackToPrevPage">
            <van-field
                    v-model="username"
                    name=""
                    label=""
                    placeholder=""
                    :rules="[{ required: true, message: '' }]"
            />
            <van-field
                    v-model="password"
                    type="password"
                    name=""
                    label=""
                    placeholder=""
                    :rules="[{ required: true, message: '' }]"
            />
            <van-field name="checkbox" label="">
                <template #input>
                    <van-checkbox v-model="rememberMe" shape="square"/>
                </template>
            </van-field>
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">
                    
                </van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        name: "Login",
        data: () => ({
            username: '',
            password: '',
            rememberMe: true
        }),
        methods: {
            ...mapActions('validate', {
                userLogin: 'userLogin'
            }),
            loginAndBackToPrevPage() {
                this.userLogin(
                    {
                        username: this.username,
                        password: this.password,
                        'remember-me': this.rememberMe
                    }
                )
                this.$router.go(-1)
            }
        }
    }
</script>

