<template>
    <van-cell style="background: #EFF0F1">
        <van-card
                :price="totalPrice(order.totalMoney)"
                :title="order.shopShopname"
        >
            <template #tags>
                <van-cell v-for="item in orderFoods" :key="item.id">
                    <van-card
                            :price="totalPrice(item.sellPrice,item.numbers)"
                            :origin-price="totalPrice(item.originPrice,item.numbers)"
                            :title="item.title+'  x'+item.numbers"
                            :thumb="item.cover"
                    >
                    </van-card>
                </van-cell>
            </template>
            <template #footer>
                <!--  -->
                <van-button
                        color="#ff3333"
                        @click="abortDialog=true"
                        v-if="order.completestatus===''"
                        size="small">
                </van-button>
                <van-button
                        color="#ff3333"
                        @click="refundDialog=true"
                        :disabled="order.completestatus===''"
                        v-if="order.completestatus==='' ||
                            order.completestatus===''||
                            order.completestatus===''"
                        size="small">
                </van-button>
                <van-button
                        @click="preDeliverSent"
                        v-if="order.completestatus===''"
                        color="#ff8000" size="small">
                </van-button>
            </template>
            <template #price-top>
                <span>{{modifiedTime(new Date(order.gmtCreate))}}</span><br/>
                <span>{{getDate(new Date(order.gmtCreate))}}</span><br/>
                <span>{{order.completestatus}}</span>
                <br/>
                <span></span>
            </template>
        </van-card>
        <van-dialog
                @confirm="sendRefund"
                v-model="refundDialog" title="" show-cancel-button>
            <van-field
                    placeholder=""
                    autofocus label=""
                    v-model="refundReason"
                    type="textarea"
                    maxlength="50"
                    show-word-limit
            ></van-field>
        </van-dialog>
        <van-dialog
                @confirm="abortOrderStatus"
                v-model="abortDialog" title="" show-cancel-button>
        </van-dialog>
    </van-cell>
</template>

<script>
    import {changeTwoDecimal_f, dateFormat} from '../../../utils/money-converter.js'
    import {Dialog, Toast} from "vant";

    export default {
        name: "OrderItem",
        props: ['order', 'removeItemFromParent', 'childRefresh'],
        computed: {},
        watch: {
            updateFlag() {
                this.$forceUpdate()
            }
        },
        methods: {
            totalPrice: (price, amount = 1) => {
                return changeTwoDecimal_f(price * amount)
            },
            apiRequestFoods(orderId) {
                this.axios
                    .get('/orderfood/' + orderId)
                    .then(
                        res => {
                            this.orderFoods = res.data.list
                        }
                    )
            },
            modifiedTime(date) {
                // date.setHours(date.getHours()+8)
                return dateFormat('HH:MM', date)
            },
            getDate(date) {
                // date.setHours(date.getHours()+8)
                return dateFormat('mmdd', date)
            },
            deliverSent(orderId) {
                this.axios
                    .get('/deliver/complete/' + orderId)
                    .then(
                        res => {
                            if (res.data.code === 0) {
                                Toast({message: '', type: 'success'})
                                // this.removeItemFromParent(this.key)
                            }
                        }
                    ).catch(
                    () => {
                        Toast({message: '', type: 'fail'})
                    }
                )
            },
            preDeliverSent() {
                Dialog.confirm({
                    title: '',
                    message: '',
                    confirmButtonText: ''
                })
                    .then(() => {
                        this.deliverSent(this.order.id)
                    })
                    .catch(() => {
                        // on cancel
                    });
            },
            apiChangeOrderStatus() {
                let refundEntity = {
                    "orderId": this.order.id,
                    "userId": this.order.userId,
                    "username": this.order.userUsername,
                    "refundReason": this.refundReason,
                    "serviceCost": 0.1,
                    "completestatus": 0,
                }
                this.axios(
                    {
                        url: '/orderdetail/refund/' + this.order.id,
                        method: 'put',
                        data: refundEntity,
                        headers: {'content-type': 'application/json'}
                    })
                    .then(
                        res => {
                            if (res.data.code === 0) {
                                Toast({message: '', type: 'success'})
                            } else {
                                Toast({message: '', type: 'fail'})
                            }
                        }
                    ).catch(
                    () => Toast({message: '', type: 'fail'})
                )
            },
            // 
            apiCommitRefund() {
                let refundEntity = {
                    "orderId": this.order.id,
                    "userId": this.order.userId,
                    "username": this.order.userUsername,
                    "refundReason": this.refundReason,
                    "serviceCost": 0.1,
                    "completestatus": 0,
                }
                this.axios({
                    url: '/user/refund/',
                    method: 'post',
                    data: refundEntity,
                    headers: {'content-type': 'application/json'}
                })
                    .then(
                        res => {
                            if (res.data.code === 0) {
                                Toast({message: '', type: 'success'})
                            } else {
                                Toast({message: '', type: 'fail'})
                            }
                        }
                    ).catch(
                    () => Toast({message: '', type: 'fail'})
                )
            },
            sendRefund() {
                this.apiCommitRefund()
                this.apiChangeOrderStatus()
                this.childRefresh()
            },
            abortOrderStatus() {
                this.apiChangeOrderStatus()
                this.childRefresh()
            }
        },
        data() {
            return {
                orderFoods: [],
                refundDialog: false,
                refundReason: '',
                abortDialog: false
            }
        },
        mounted() {
            this.apiRequestFoods(this.order.id)
        },
        beforeRouteLeave(to, from, next) {
            this.orderFoods = []
            next()
        }
    }
</script>

<style scoped>
    .van-card__thumb {
        position: relative;
        -webkit-box-flex: 0;
        -webkit-flex: none;
        flex: none;
        width: 40px;
        height: 40px;
        margin-right: 8px;
    }

    .van-card__content {
        position: relative;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        flex: 1;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        flex-direction: column;
        -webkit-box-pack: justify;
        -webkit-justify-content: space-between;
        justify-content: space-between;
        min-width: 0;
        min-height: 50px;
    }
</style>